    background-image: linear-gradient(to bottom, black 0.5 25%, white);
    background-image: url(../images/cover.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center; }
    .page__header--hero h1 {
      padding-top: 50vh;
      padding-bottom: 10px;
      color: white;
      text-transform: uppercase;
      line-height: 55px;
      font-size: 42px;
      text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);
      margin: 0; }
    .page__header--hero p {
      color: white;
      padding-bottom: 45px; }
    .page__header--hero a {
      width: 45px;
      height: 45px;
      border-radius: 50%;
      background-color: white; }
      .page__header--hero a img {
        padding-top: 18px; }

.page__main {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .page__main--section1 {
    padding: 30px 15px 45px 15px; }
    .page__main--section1 small {
      color: #8ccbea;
      font-size: 14px;
      line-height: 30px;
      text-transform: capitalize; }
    .page__main--section1 h2 {
      font-size: 24px;
      line-height: 30px;
      padding-bottom: 15px; }
    .page__main--section1 p {
      color: #4a4a4a;
      font-size: 18px;
      line-height: 30px;
      padding-bottom: 30px; }
    .page__main--section1 a {
      color: #f26002;
      font-size: 20px;
      line-height: 45px; }
  .page__main--section2 {
    background-color: #eff9ff;
    padding: 30px 15px 60px 15px; }

.page__footer {
  background-color: #304268;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center; }
  @media all and (min-width: 768px) {
